$lightGrey: #f7f7f7;

@mixin useInter($size: 1em, $weight: 400) {
  font-family: "Inter", sans-serif;
  font-size: $size;
  font-weight: $weight;
}

.arrow {
  margin-right: 0.8em;
  font-size: 0.7em;
  font-family: fantasy;
  display: inline-block;
  &.rotated {
    transform: rotate(90deg);
  }
  cursor: default;
}

%AppButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  cursor: pointer;

  border-radius: 3px;
  border: 1px solid black;

  span {
    @include useInter(1em, 500);
    font-variant: all-small-caps;
    color: black;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
  }

  &:hover {
    background-color: $lightGrey;
  }

  &:active {
    background-color: white;
  }
}

.layerType {
  &.selected {
    font-weight: 600;
    color: black;
  }

  cursor: pointer;
  padding: 3px 5px 3px 5px;
}

.buttonGroup {
  display: flex;
  flex-direction: row;
}

.JSONView {
  flex: 1;
  flex-grow: 1;
  height: 400px;
  overflow: auto;
}

.deleteLayerBtn {
  font-size: 1.2em;
  @extend %AppButton;
  border: 0px;
  margin-left: auto;
}

.duplicateLayerBtn {
  font-size: 1em;
  @extend %AppButton;
  border: 0px;
  margin-left: auto;
}

.copyBtn {
  position: fixed;
  @extend %AppButton;
  border: 0px;
  margin-top: 2px;
  margin-right: 20px;
  margin-left: auto;
  padding: 5px;
  right: 0px;
  background-color: $lightGrey;
  > span {
    padding: 5px;
  }
}

.layerItemBtns {
  display: flex;
  flex-direction: row;
  margin-left: auto;
}

.layerItem .placeholder {
  height: 28px;
  border: 0.5px dashed;
}

.layerItem.beingDragged {
  display: block;
}

.dropZone {
  height: 4px;
}

.dropZone.draggedOver {
  height: 20px;
  border: 0.5px dashed;
}

.layerItemMain {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.addLayerBtn {
  margin: 5px 0px 10px 0px;
}

.layerPanel {
  height: calc(100vh - 300px);
  overflow: auto;
}

.pointInputContainer {
  display: flex;
  flex-direction: row;
  margin-bottom: 0px;
}

.symbolPropertyItem {
  padding-bottom: 10px;
}

.SymbolPropertyEditing {
  & > span {
    display: block;
    padding-bottom: 15px;
    text-transform: uppercase;
  }
}

.addSymbolBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  border: dashed 1px;
  cursor: pointer;
}

.addWebStyle {
  width: 100%;
}
